import ProductChip from "../elements/ProductChip";

// get api로 데이터 받아와서 배열로 뿌리는 통신기능만 수행, css는 따로 넘기기

export default function ProductChipComponent() {

    const productListOriginal = fakedata;
    const productListReverse = [...productListOriginal].reverse();
    const productListSliced = productListReverse.slice(0, 15);

    return (
        <>
            {
                productListSliced.map(d => (
                    <ProductChip key={d.product_id} data={d} />
                ))
            }

        </>
    )
}

